.controller{

    background-color: antiquewhite;
    padding: 0;
    border: mediumpurple 1px solid;
    border-radius: 10px;
    text-align: center;
  }
  .controller p{
    text-align: center;
    padding:5px;
    font-weight: 700;
    font-size: x-large;
  }
  
  .btn-style{
    background: #b375af;
    font: inherit;
    border: 1px solid #5101d1;
    border-radius: 3px;
    margin: 5px;
    
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.26);
    color: #06010c;
    cursor: pointer;
  }
 
 

  .input-style{
    margin: 3px;
    text-align: center;
  }